
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import MPanel from '@/components/common/MPanel'

import { ENETUNREACH } from 'constants';

export default {
components: {
Scroller,
Spinner,
MPanel,
LoadMore
},props: {
height:String,
moreLink: [String,Object],
type: {
type: String,
default: '1'
},
dataType:String,
searchText:String,
condition:{
type: Object,
default () {
return {}
}
},
usePulldown: {
type: Boolean,
default: true
},
usePullup: {
type: Boolean,
default: true
}
},
methods: {
loadMore () {
setTimeout(() => {
this.pageIndex++
this.loadData()
}, 600)
},
refreshDown () {
this.refresh(true)
},
refresh (isDown) {
this.pageIndex=0
if(!isDown)
this.refreshing=true
this.list=[]
setTimeout(() => {
this.loadData(()=>{
setTimeout(() => {
this.usePulldown&&this.$refs.scroller.donePulldown()
this.$refs.scroller.reset({top:0})
this.usePullup&&!this.moreLink&&this.$refs.scroller.enablePullup()
}, 10)
})
}, 600)
},
loadData(callback){
var con = { pageIndex:this.pageIndex }
if(this.search)
con.search = this.search
con = this.mixCon(this.condition,con)
console.log(this.dataType,con)
queryInfo({mod:this.dataType,con:con }).then((res)=>{
var data = res.data
console.log(data)
this.list = this.list.concat(data)
setTimeout(() => {
this.usePullup&&this.$refs.scroller.donePullup()
if(this.moreLink||!data||data.length<10)
this.usePullup&&this.$refs.scroller.disablePullup()
}, 10)
callback&&callback()
this.refreshing = false
})
},
mixCon(s,t){
for (const key in s) {
if (s.hasOwnProperty(key)) {
t[key] = s[key]
}
}
return t
},
onImgError (item, $event) {
console.log(item, $event)
},
emitEvent(type,v){
this.$emit(type,v)
}
},
data () {
return {
list:[],
pageIndex:0,
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
search:'',
refreshing:false
}
},
computed:{
footer(){
if(!this.moreLink||!this.list||this.list.length<1)
return null
return {title:'查看更多', url:this.moreLink}
},
showLoading(){
return this.refreshing&&this.pageIndex==0
}
},
mounted(){
this.refreshing=true
this.loadData(()=>{
})
},
watch:{
searchText(val){
console.log('searchText',val)
if(typeof(val)=='undefined'||val == null)
return
if(this.search == val)
return
this.search = val
this.refresh()
},
condition:{
handler(n,o){
console.log('watch condition',this.condition)
this.refresh()
},
deep:true
}
}
}



